<template lang="pug">
.contentContainer
    h1.TitleU {{name}}
    h2.subTitle Keynote Speakers
    .speakerContainer(v-for="speaker in speakers" data-aos="fade-up" data-aos-duration="1000")
        .speakerImg(:style="{'background-image': 'url(' + speaker.img + ')'}")
        .staffListContentBlock
            h2.staffListName {{speaker.name}}
            .staffListTitle {{speaker.agency}}
            h3 {{speaker.keynote}} :
            p {{speaker.content}}
</template>

<script>

export default {
    components: {

    },
    data () {
        return {
            name: "KEYNOTES",
            speakers: [
                {
                    img: "http://plopes.org/wp-content/uploads/2019/05/PedroLopes_UChicago2.jpg",
                    name: "Pedro Lopes",
                    agency: "University of Chicago, USA",
                    keynote: "Keynote 1",
                    content: "(To be announced)"
                },
                {
                    img: "https://pbs.twimg.com/profile_images/502132318354419713/UXyxLGjS_400x400.jpeg",
                    name: "Mike Chen",
                    agency: "National Taiwan University, Taiwan",
                    keynote: "Keynote 2",
                    content: "(To be announced)"
                }
            ]
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
@import "@/style/value.sass"
.speakerContainer 
    display: flex
    flex-wrap: wrap
    margin: 40px 0
.speakerImg 
    width: 240px
    height: 240px
    background-position: center
    background-size: cover

.staffListTitle
    font-size: 19px
    width: 100%
    font-family: 'Roboto', sans-serif

.staffListName
    font-weight: bold
    width: 100%
    font-family: 'Roboto', sans-serif
    color: #e8b279
    font-weight: bold
    margin-top: 0

.staffListContentBlock
    margin-left: 40px
    padding-top: 10px

@media (max-width: 960px)
    .staffListContentBlock
        padding-top: 15px
        margin-top: 0 !important

</style>



